
import React from "react";
import { runForceGraph } from "./ForceGraphGenerator";
import styles from "./forceGraph.module.css";

export function ForceGraph({ linksData, nodesData, nodeHoverTooltip, mytemp }) {
  const containerRef = React.useRef(null);
  
  React.useEffect(() => {
    let destroyFn;

    if (containerRef.current) {
		
      const { destroy, update, nodes } = runForceGraph(containerRef.current, linksData, nodesData, nodeHoverTooltip);
      destroyFn = destroy;
      
      mytemp(update)
    }

    return destroyFn;
  }, [nodesData]);

  return <div ref={containerRef} className={styles.container} />;
}