
import React, { Component } from 'react'
import MyNavbar from './components/Navbar'

import { ForceGraph } from './components/graphing/ForceGraph'
import data from './data/sample.json'
import Subjects from './components/Subjects'
import SemesterPlan from './components/Semesterplan'

class App extends Component {
  state = {
    selected: [],
	topsorted: [],
	sortBy: "",
  };

  //
  addSubs = (e) => {
    const node = e.currentTarget.getAttribute("data-item");
    const temp = data.nodes.filter((a) => a.code === node)[0];

    const { selected, thisfunc } = this.state;
	
    if (!selected.includes(temp)) {
		const result = this.recursiveAdd([temp])
		const result2 = result.filter((n) => !selected.includes(n));
		const result3 = [...new Set(result2)]
		
		this.setState({
      		selected: [...this.state.selected, ...result3],
    	});
    }
	
	else {
		this.setState({
			selected: selected.filter((sub) => sub !== temp),
		});
	}
	thisfunc()
  };
  
  recursiveAdd = (nodeList) => {
	
	let result = [...nodeList]
	
	for (const node of nodeList) {
		
		if (node.msubs != null) {
			let rec = data.nodes.filter((n) => node.msubs.includes(n.id))
			result = [...result, ...this.recursiveAdd(rec)];
		}
		
		if (node.rsubs != null) {
			let rec = data.nodes.filter((n) => node.rsubs.includes(n.id))
			result = [...result, ...this.recursiveAdd(rec)];
		}
	}
	
	return result
  }
  
  clearSubs = () => {
		const { thisfunc } = this.state;
	
		this.setState({
			selected: [],
		});
		
		thisfunc()
  }

  //
  setFunc = (e) => {
    this.setState({
      thisfunc: e,
    });
  };
  
    //
  tsFunc = (e) => {
    this.setState({
      topsorted: e,
    });
  };
  
  chooseSort = (e) => {
	this.setState({
		sortBy: e.currentTarget.getAttribute("data-item")
	})
  }

  //
  nodeHoverTooltip = (node) => {
	return node.id + " " + node.title
  }

  render() {
    const { sortBy, topsorted, selected, thisfunc } = this.state;

    return (
      <div className="App">
        <MyNavbar></MyNavbar>
        <div className="app-container">
          <section className="left">
            <Subjects
              nodesData={data.nodes}
              selected={selected}
              sortBy={sortBy}
              funcSelect={this.addSubs}
              funcSort={this.chooseSort}
              funcClear={this.clearSubs}
            />
          </section>

          <section className="middle">
            {/* <SemesterPlan
				nodesData={selected}
				linksData={data.links}
				result={topsorted}
				tsFunc={this.tsFunc}
			/> */}
          </section>

          <section className="right">
            <ForceGraph
              linksData={data.links}
              nodesData={selected}
              nodeHoverTooltip={this.nodeHoverTooltip}
              mytemp={this.setFunc}
            />
          </section>
        </div>
      </div>
    );
  }
}

export default App